import {Baobab, cloneDeep} from 'src/vendor';

const tree = new Baobab(getDefaultState(), {
    cloningFunction: cloneDeep,
    clone: true
});

export default tree;

function getDefaultState() {
    return {
        errors: {},
        game: {
            started: false,
            symbols: ['X', '0'],
            symbol: 0,
            positions: []
        }
    };
}
